body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app {
  background-color: #4682B4;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.character {
  font-size: 10vmin;
  position: relative;
  transition: all 0.1s;
}

.cooldown-ui {
  position: absolute;
  bottom: 20px;
  width: 50px;
  height: 50px;
  font-size: 40px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cooldown-border {
  width: 100%;
  height: 100%;
  border: 2px solid black;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cooldown-fill {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(128, 128, 128, 0.7);
  transition: height 0.1s linear;
  z-index: 1;
}

.cooldown-text {
  font-style: normal;
  font-size: 50px;
  position: relative;
  z-index: 2;
  color: black;
}

.cooldown-ui.z-cooldown {
  right: 80px;
}

.cooldown-ui.x-cooldown {
  right: 20px;
}

.cooldown-ui.x-cooldown .cooldown-fill {
  background-color: rgba(128, 128, 128, 0.7);
}

.fullscreen-button {
  position: absolute;
  top: 20px;
  left: 20px;
  padding: 10px 20px;
  background-color: #333;
  color: #fff;
  border: none;
  cursor: pointer;
}
